export default {
  data() {
    return {
      name: '',
      comment: '',
      error: false,
    };
  },
  methods: {
    addComment(e) {
      e.preventDefault();
      if (this.name !== '' && this.comment !== '') {
        this.$emit('addComment', { name: this.name, comment: this.comment });
        this.name = '';
        this.comment = '';
      } else {
        this.error = true;
        setTimeout(() => {
          this.error = false;
        }, 3000);
      }
    },
  },
};
